<template>
  <div class="relative w-full">
    <div
      class="relative rounded-08 border border-solid"
      :class="[
        errorMessage
          ? 'border-constant-status-error text-constant-status-error'
          : 'border-stroke-bold text-content-on-neutral-secondary focus-within:border-constant-brand-primary focus-within:text-constant-brand-primary',
        isDisabled ? 'cursor-not-allowed' : '',
      ]"
    >
      <label v-if="label" class="floating-form-select-label absolute">
        {{ label }}
      </label>
      <div
        @click="toggleDropDown"
        class="flex w-full appearance-none justify-between bg-transparent p-spacing-200 py-spacing-150 text-left text-body/m/relaxed/500 text-content-on-neutral-primary antialiased focus:outline-none ipad:text-body/m/relaxed/500"
        :tabindex="isDisabled ? undefined : '0'"
        :class="isDisabled ? 'cursor-not-allowed' : 'cursor-pointer'"
        @focus="handleFocus"
        @blur="handleBlur"
        ref="dropdown"
        @keydown.stop="handleKeydown"
      >
        {{ selectedOption ? getSelectedOptionValue : getDefaultOptionValue }}
        <IconChevronDown
          class="h-spacing-250 w-spacing-250 text-content-on-neutral-primary"
          :class="
            isDropdownOpen
              ? 'rotate-180 transition-all duration-300'
              : 'rotate-0 transition-all duration-300'
          "
        />
      </div>

      <div
        v-if="isDropdownOpen && !isDisabled"
        class="absolute left-spacing-0 right-spacing-0 z-[100] mt-spacing-25 max-h-[250px] w-full overflow-y-auto rounded-08 bg-bg-neutral-default shadow-card"
      >
        <p
          v-for="(option, index) in options"
          :key="index"
          ref="optionRefs"
          @mousedown.prevent="selectOption(option)"
          :class="
            index === highlightedIndex
              ? 'bg-bg-neutral-disabled'
              : 'bg-bg-neutral-raised hover:bg-bg-neutral-disabled'
          "
          class="cursor-pointer border-b border-stroke-bold px-spacing-150 py-spacing-150 text-body/m/relaxed/400 text-content-on-neutral-primary"
        >
          {{ option?.value }}
        </p>
      </div>
    </div>
    <p
      v-if="showError"
      class="mt-spacing-50 min-h-spacing-200 text-left text-body/s/relaxed/400 text-constant-status-error"
    >
      {{ errorMessage ? errorMessage : "" }}
    </p>
  </div>
</template>

<script>
/**
 * @emits option-selected - Emitted when the option value changes.
 * @param {Object} value - The current input value.
 */

export default {
  props: {
    options: {
      type: Array,
      required: true,
      default: () => [],
    },
    errorMessage: {
      type: String,
      default: "",
    },
    label: {
      type: String,
      required: false,
      default: "",
    },
    selectedOptionKey: {
      type: String,
      required: false,
    },
    isDisabled: {
      type: Boolean,
      required: false,
      default: false,
    },
    showError: {
      type: Boolean,
      default: true,
    },
  },
  components: {
    IconChevronDown: () => import("@/components/svgIcons/chevron-down.vue"),
  },
  data() {
    return {
      isDropdownOpen: false,
      highlightedIndex: -1,
      selectedOption: this.selectedOptionKey,
    };
  },
  watch: {
    selectedOptionKey() {
      this.selectedOption = this.selectedOptionKey;
    },
  },
  computed: {
    getSelectedOptionValue() {
      return this.options?.find((option) => option?.key === this.selectedOption)
        ?.value;
    },
    getDefaultOptionValue() {
      const defaultOption = this.options?.find((opt) => opt?.default);
      return defaultOption ? defaultOption?.value : "Select an option";
    },
  },
  methods: {
    handleFocus() {
      if (this.isDisabled) {
        return false;
      }
      this.$emit("focus");
      return true;
    },
    handleBlur() {
      if (this.isDisabled) {
        return false;
      }
      this.$emit("blur");
      this.isDropdownOpen = false;
      this.highlightedIndex = -1;
      return true;
    },
    toggleDropDown() {
      if (this.isDisabled) {
        return;
      }
      this.isDropdownOpen = !this.isDropdownOpen;
      if (!this.isDropdownOpen) {
        this.highlightedIndex = -1; // Reset highlighted index on close
      }
    },
    selectOption(option) {
      this.selectedOption = option?.key;
      this.isDropdownOpen = false;
      this.highlightedIndex = -1;
      this.$emit("option-selected", option?.key);
      this.$emit("option-selected-full", option);
    },
    navigateOptions(direction) {
      if (this.options.length === 0) return;
      this.highlightedIndex =
        (this.highlightedIndex + direction + this.options.length) %
        this.options.length;

      // Scroll the highlighted suggestion into view
      this.$nextTick(() => {
        const optionElement = this.$refs.optionRefs?.[this.highlightedIndex];
        if (optionElement) {
          optionElement.scrollIntoView({
            behavior: "smooth",
            block: "nearest",
          });
        }
      });
    },
    handleKeydown(event) {
      if (this.isDisabled) {
        return;
      }
      if (["ArrowDown", "ArrowUp", "Enter", "Escape"].includes(event.key)) {
        event.preventDefault();
      }
      if (event.key === "ArrowDown") {
        this.navigateOptions(1);
      } else if (event.key === "ArrowUp") {
        this.navigateOptions(-1);
      } else if (event.key === "Enter") {
        if (this.highlightedIndex >= 0) {
          this.selectOption(this.options[this.highlightedIndex]);
        } else if (!this.isDropdownOpen) {
          this.isDropdownOpen = true;
        }
      } else if (event.key === "Escape") {
        this.isDropdownOpen = false;
      }
    },
  },
};
</script>
